.profile {
  display: flex;
}

.profile-left {
  width: 64px;
  height: 100vh;
  background: #fbfbfb;
}

.profile-left a {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
}

.profile-left a:after {
  content: "";
  width: 28px;
  height: 28px;
  background: url("backIcon.94f7948b.svg") 0 0 / cover;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.profile-content {
  flex: auto;
  justify-content: center;
  align-items: center;
  display: flex;
}

.profile-content__img {
  justify-content: center;
  display: flex;
}

.profile-content__main {
  margin-top: 60px;
}

.profile-content__name {
  text-align: center;
  color: #1e1e1e;
  margin-top: 21px;
  font-size: 16px;
  font-weight: 600;
  line-height: 16px;
}

.profile-content__option {
  flex-direction: column;
  gap: 10px;
  margin-top: 60px;
  display: flex;
}

.profile-content__option a {
  border-bottom: 1px solid #eaeaea;
  padding-bottom: 10px;
  font-size: 13px;
}

.profile-content__option a:last-child {
  border-bottom: 0;
  padding-bottom: 0;
}

.personLogo {
  width: 130px;
  height: 130px;
  background: #efefef;
  border-radius: 50%;
  position: relative;
}

.personLogo.empty {
  position: relative;
}

.personLogo.empty:after {
  content: "";
  width: 40px;
  height: 40px;
  background: url("clear.0a7353ba.svg") 0 0 / cover;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.personLogo:hover .personLogo__change {
  cursor: pointer;
  display: flex;
}

.personLogo__change {
  z-index: 1;
  text-align: center;
  opacity: .5;
  width: 100%;
  height: 100%;
  color: #fff;
  background: #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 13px;
  font-weight: 500;
  line-height: 14px;
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.personLogo-set {
  width: 100%;
  height: 100%;
  z-index: 10;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.personLogo-set.open {
  display: block;
}

.personLogo-set__bg {
  width: 100%;
  height: 100%;
  opacity: .4;
  cursor: pointer;
  background: #000;
  position: absolute;
  top: 0;
  left: 0;
}

.personLogo-set__body {
  width: 340px;
  z-index: 2;
  background: #fff;
  border-radius: 12px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px 30px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 6px #00000024;
}

.personLogo-set__title {
  color: #1e1e1e;
  font-size: 15px;
  font-weight: 500;
  line-height: 20px;
}

.personLogo-set__input {
  text-align: center;
  height: 32px;
  margin-top: 43px;
  margin-bottom: 45px;
  position: relative;
}

.personLogo-set__input input {
  opacity: 0;
  z-index: -1;
  position: absolute;
}

.personLogo-set__input label {
  color: #3369f3;
  width: 122px;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  text-decoration: underline;
  display: block;
}

.personLogo-set__input label.file {
  color: #8d8d8d;
  font-size: 14px;
  text-decoration: none;
}

.infoField {
  width: 510px;
  justify-content: space-between;
  display: flex;
}

.infoField input {
  text-align: right;
  color: #999;
  background: none;
  border: none;
  outline: none;
  font-size: 13px;
  font-weight: 500;
  line-height: 13px;
}

.infoField label {
  color: #1e1e1e;
  font-size: 13px;
  font-weight: 500;
  line-height: 13px;
}

